.sidebar {
  height: 100vh;
  width: 19%;
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  padding: 92px 40px 80px;
  position: fixed;
  z-index: 10;
  transition: all .2s cubic-bezier(0.65, 0.05, 0.36, 1) 0s;
  &-nav {
    display: flex;
    flex-direction: column;
    &__link {
      font-family: "Sofia Pro Regular", sans-serif;
      font-weight: 400;
      font-size: 31px;
      line-height: 31px;
      color: #000000;
      margin-bottom: 6px; } }
  &--active {
    left: 0 !important; } }
